import { createRouter, createWebHashHistory } from 'vue-router';
import HomeView from '../views/HomeView.vue';
import ManualView from '../views/ManualView.vue';
import ApplicabilitySheetView from '../views/ApplicabilitySheetView.vue';
import ApplicabilitySheetValidatorView from '../views/ApplicabilitySheetValidatorView.vue';
import ApplicabilityValidatorView from '../views/ApplicabilityValidatorView.vue';

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView
  },
  {
    path: '/manual',
    name: 'manual',
    component: ManualView
  },
  {
    path: '/applicability-sheet',
    name: 'applicability-sheet',
    component: ApplicabilitySheetView
  },
  {
    path: '/applicability-sheet-validator',
    name: 'applicability-sheet-validator',
    component: ApplicabilitySheetValidatorView
  },
  {
    path: '/applicability-validator',
    name: 'applicability-validator',
    component: ApplicabilityValidatorView
  },
]

const router = createRouter({
  history: createWebHashHistory(),
  routes
})

export default router
