<template>
  <div>
    <div class="background"></div>
    <div id="main-container" class="p-5 text-center border rounded-5 shadow bg-white">
      <h1>APPLICABILITY SHEET GENERATOR</h1>
      <div class="text-container">
        <p>This application allows you to generate a new excel file with all the information regarding the applicability of an MPD. With this file, you will be able to fill it with the information for the corresponding aircraft, and then, use it to get automatically for each task in the MPD if applies or not.</p>
      </div>
      <p class="input-label">MPD file:</p>
      <input class="form-control mt-4" type="file" accept=".csv, application/vnd.openxmlformats-officedocument.spreadsheetml.sheet, application/vnd.ms-excel" @change="readFile">
      <button class="btn btn-primary mt-5" :class="[this.workbook === '' ? 'disabled' : '']" @click="transformFile">TRANSFORM</button>
      <a class="btn mt-3" :class="[this.download_workbook === '' ? 'disabled btn-secondary' : 'btn-success']" @click="downloadFile">DOWNLOAD</a>
    </div>
  </div>
</template>

<script>
import { checkIfSelectable, reconstrucArr, getAllApplicabilityOptions, getSelectedGroupOptions, generateSheetData } from "@/js-modules/applicabilitySheetFunctions";
const XLSX = require("xlsx");

export default {
  name: 'ApplicabilitySheet',
  data() {
    return {
      workbook: "",
      download_worksheet: "",
      download_workbook: "",
    }
  },
  methods: {
    // onChange(event) {
    //   this.file = event.target.files ? event.target.files[0] : null;
    //   if (this.file) {
    //     const reader = new FileReader();

    //     reader.onload = (e) => {
    //       /* Parse data */
    //       const bstr = e.target.result;
    //       const wb = XLSX.read(bstr, { type: 'binary' });
    //       /* Get first worksheet */
    //       const wsname = wb.SheetNames[3];
    //       const ws = wb.Sheets[wsname];
    //       /* Convert array of arrays */
    //       const data = XLSX.utils.sheet_to_json(ws, { header: 1 });
          
    //       console.log(event)
    //     }

    //     reader.readAsBinaryString(this.file);
    //   }
    // },
    readFile() {
      const input = document.querySelector("input");
      this.file = input.files[0];
      const reader = new FileReader();

      reader.onload = (e) => {
        /* Parse data */
        const bstr = e.target.result;
        const wb = XLSX.read(bstr, { type: 'binary' });
        // console.log(wb);
        this.workbook = wb;
      }

      reader.readAsBinaryString(this.file);
      // console.log(reader.readAsBinaryString(this.file));
      // return reader.readAsBinaryString(this.file);
    },
    transformFile() {
      // Select the excel sheet
      const MPD_sheet = this.workbook.Sheets["MPD"];

      // Transform fron excel to JSON
      const arr = XLSX.utils.sheet_to_json(MPD_sheet)

      // Getting the first row with data, as well as the APPLICABILITY column.
      let startingRow = 0;
      let key;

      while(startingRow <= 5) {
        if(Object.values(arr[startingRow]).includes("APPLICABILITY")) {
          key = Object.keys(arr[startingRow])[(Object.values(arr[startingRow])).indexOf("APPLICABILITY")];
          break;
        }
        startingRow++;
      }

      // Getting all the different variations from the APPLICABILITY
      const slicedArr = arr.slice(startingRow+1).map(x => x[key]);
      const applicabilityOptions = getAllApplicabilityOptions(slicedArr.map(a => {
          return reconstrucArr(a.replaceAll(/[ ]{1,}/g, " ").split("\n").map(e => e.trim()));
      }));

      // Sorting the cases by group, and each group alphabetically
      const sortedApplicabilityOptions = ([
          ...getSelectedGroupOptions(applicabilityOptions, "group 1").sort(),
          ...getSelectedGroupOptions(applicabilityOptions, "group 2").sort(),
          ...getSelectedGroupOptions(applicabilityOptions, "group 3").sort(),
          ...getSelectedGroupOptions(applicabilityOptions, "group 4").sort(),
          ...getSelectedGroupOptions(applicabilityOptions, "group 5").sort(),
          ...getSelectedGroupOptions(applicabilityOptions, "group 6").sort(),
        ]);

      // Creating the data with the structured required to convert to a sheet
      const result = generateSheetData(sortedApplicabilityOptions).map(x => ({
        "APPLICABILITY CASES": x.col1,
        "SB LIST (if apply)": x.col2,
        "POST": x.col3,
        "MOD": x.col4,
        "SB": x.col5,
        "TBD": x.col6,
        "N/A": ""
      }));
      console.log(result)
      // Creating the new worksheet and workbook
      this.download_worksheet = XLSX.utils.json_to_sheet(result);
      this.download_workbook = XLSX.utils.book_new();
      const sheetName = "Applicability cases";
      this.download_workbook.SheetNames.push(sheetName);
      this.download_workbook.Sheets[sheetName] = this.download_worksheet;
      // this.download_workbook = this.workbook;
    },
    downloadFile() {
      let name = "file";
      XLSX.writeFile(this.download_workbook, `${name}.xlsx`);
    },
  },
  mounted() {
    // console.log("PRE SB V2500- ENG-72-0564".match(/^(PRE )?SB V\d{3,5}.*[^-]$/));
  }
}
</script>

