<template>
  <div>
    <div class="background"></div>
    <div id="main-container" class="p-5 text-center border rounded-5 shadow bg-white">
      <h1>APPLICABILITY VALIDATOR</h1>
      <div class="text-container">
        <p>With this application you will be able to get the results of the applicability. For that it is necessary to upload the MPD file corresponding the aircraft, and the excel sheet with all the applicability cases filled with the information for the corresponding aircraft.</p>
      </div>
      <p class="input-label">MPD file:</p>
      <input class="form-control mt-4" type="file" accept=".csv, application/vnd.openxmlformats-officedocument.spreadsheetml.sheet, application/vnd.ms-excel" @change="readFile('mpd')">
      <p class="input-label">Applicability sheet file:</p>
      <input class="form-control mt-4" type="file" accept=".csv, application/vnd.openxmlformats-officedocument.spreadsheetml.sheet, application/vnd.ms-excel" @change="readFile('sheet')">
      <button class="btn btn-primary mt-5" :class="[this.MPDworkbook === '' ? 'disabled' : '']" @click="transformMPDFile">TRANSFORM</button>
      <a class="btn mt-3" :class="[this.download_workbook === '' ? 'disabled btn-secondary' : 'btn-success']" @click="downloadFile">DOWNLOAD</a>
    </div>
  </div>
</template>

<script>
import { reconstrucArr, MPDandSheetCheckerIfAllCasesMatch, applicabilityCheckerArrCreator } from "@/js-modules/applicabilitySheetFunctions";
const XLSX = require("xlsx");

export default {
  name: 'ApplicabilitySheet',
  data() {
    return {
      MPDworkbook: "",
      SHEETworkbook: "",
      download_worksheet: "",
      download_workbook: "",
    }
  },
  methods: {
    readFile(content) {
      let input;
      if(content === "mpd") input = document.querySelector("input");
      else if(content === "sheet") input = document.querySelectorAll("input")[1];
      this.file = input.files[0];
      const reader = new FileReader();

      reader.onload = (e) => {
        /* Parse data */
        const bstr = e.target.result;
        const wb = XLSX.read(bstr, { type: 'binary' });
        if(content === "mpd") this.MPDworkbook = wb;
        else if(content === "sheet") this.SHEETworkbook = wb;
      }

      reader.readAsBinaryString(this.file);
    },
    transformMPDFile() {
      // Select the excel sheet
      const MPD_sheet = this.MPDworkbook.Sheets["MPD"];

      // Transform fron excel to JSON
      const arr = XLSX.utils.sheet_to_json(MPD_sheet)

      // Getting the first row with data, as well as the APPLICABILITY column.
      let startingRow = 0;
      let key;

      while(startingRow <= 5) {
        if(Object.values(arr[startingRow]).includes("APPLICABILITY")) {
          key = Object.keys(arr[startingRow])[(Object.values(arr[startingRow])).indexOf("APPLICABILITY")];
          break;
        }
        startingRow++;
      }

      // Getting all the different variations from the APPLICABILITY
      const slicedArr = arr.slice(startingRow+1).map(x => x[key]);
      const applicabilityOptions = slicedArr.map(a => {
          return reconstrucArr(a.replaceAll(/[ ]{1,}/g, " ").split("\n").map(e => e.trim()));
      });
      // console.log(applicabilityOptions);

      // Select the excel sheet
      const sheet_sheet = this.SHEETworkbook.Sheets["Applicability cases"];

      // Transform fron excel to JSON
      const sheetArr = XLSX.utils.sheet_to_json(sheet_sheet);
      
      // Checking if, for each cell of the MPD file, all the cases are in the sheet previously created. Print in case there is some not found
      // MPDandSheetCheckerIfAllCasesMatch(applicabilityOptions, sheetArr);

      
      // Checking each row if applies or not
      // console.log(applicabilityOptions.slice(476,477)[0].split("<AND>").filter(x => x.includes("sub<OR|AND"))[0].split("sub<OR|AND>sub"))
      const applicabilityResultColumn = applicabilityCheckerArrCreator(applicabilityOptions, sheetArr);
      // console.log(applicabilityResultColumn.map((x, i) => ({x, i: i+4, value: applicabilityOptions[i]})).filter(x => x.x === undefined || x.x === "APP"));  // to check if works when sheet is empty
      
      // Getting the task reference column
      const taskRefKey = Object.keys(arr[startingRow])[(Object.values(arr[startingRow])).indexOf("TASK\nNUMBER")];
      const taskRefArr = arr.slice(startingRow+1).map(x => x[taskRefKey]);
      
      // Creating the data with the structured required to convert to a sheet
      const result = applicabilityResultColumn.map((x, i) => ({
        "TASK NUMBER": taskRefArr[i],
        "APPLICABILITY": slicedArr[i],
        "RESULT": x,
      }));
      console.log(result)
      // Creating the new worksheet and workbook
      this.download_worksheet = XLSX.utils.json_to_sheet(result);
      this.download_workbook = XLSX.utils.book_new();
      const sheetName = "Applicability";
      this.download_workbook.SheetNames.push(sheetName);
      this.download_workbook.Sheets[sheetName] = this.download_worksheet;
      // this.download_workbook = this.workbook;

    },
    downloadFile() {
      let name = "file";
      XLSX.writeFile(this.download_workbook, `${name}.xlsx`);
    },
  },
  mounted() {
    // console.log("PRE SB V2500- ENG-72-0564".match(/^(PRE )?SB V\d{3,5}.*[^-]$/));
    // console.log([1,1,1,1,1,1,1].every((val, _, arr) => val === arr[0]));
  }
}
</script>

