<template>
  <div>
    <div class="background"></div>
    <div id="main-container" class="p-5 text-center border rounded-5 shadow bg-white">
      <h1 class="mb-5">APPLICABILITY SHEET VALIDATOR</h1>
      <p>This tool allows you to check if the applicability sheet, with all the information regarding the applicability of an MPD, is correctly filled, and can be used in the process to automatically check if an MPD task applies or not.</p>
      <input class="form-control mt-4" type="file" accept=".csv, application/vnd.openxmlformats-officedocument.spreadsheetml.sheet, application/vnd.ms-excel" @change="readFile">
      <button class="btn btn-primary mt-3" :class="[this.workbook === '' ? 'disabled' : '']" @click="validateFile">VALIDATE</button>
      <div v-if="wrongRows.length !== 0" class="mt-4">
        <p>The numbers listed below are the file rows that have something wrong:</p>
        <ul>
          <li v-for="(row, index) in wrongRows" :key="index">
            Row number {{ row }}
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
import { excelFileValidator } from "@/js-modules/applicabilitySheetFunctions";
const XLSX = require("xlsx");

export default {
  name: 'ApplicabilitySheetValidator',
  data() {
    return {
      workbook: "",
      wrongRows: [],


      download_worksheet: "",
      download_workbook: "",
    }
  },
  methods: {
    readFile() {
      const input = document.querySelector("input");
      this.file = input.files[0];
      const reader = new FileReader();

      reader.onload = (e) => {
        /* Parse data */
        const bstr = e.target.result;
        const wb = XLSX.read(bstr, { type: 'binary' });
        this.workbook = wb;
      }

      reader.readAsBinaryString(this.file);
    },
    validateFile() {
      // Select the excel sheet
      const sheet = this.workbook.Sheets["Applicability cases"];

      // Transform fron excel to JSON
      const arr = XLSX.utils.sheet_to_json(sheet);

      // Validate each row of the file with the correct format
      this.wrongRows = excelFileValidator(arr.slice(0, 10));

    },
    downloadFile() {
      let name = "file";
      XLSX.writeFile(this.download_workbook, `${name}.xlsx`);
    },
  },
  mounted() {
    // console.log("aa22".replaceAll(/[ ]{1,}/g, " "));
  }
}
</script>

